@import "../src/Styles/variables";

.App {
  // text-align: center;
  body {
    margin: 0;
    // font-family: "IBM Plex Sans";
    background-color: $prime;
    color: #ffffff;
    overflow-x: hidden;
  }

  .align-left {
    align-items: end;
    text-align: left;
    font-family: "IBM Plex Sans";
  }
  .align-right {
    align-content: start;
    direction: rtl;
    text-align: right;
    font-family: "IBM Plex Sans Arabic";
  }
}
